.supplement {
  padding: rem(90) rem(15);
  background-image: url(../img/bg02.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  &__box {
    margin-top: rem(60);
    border: rem(2) solid #fff;
    display: flex;
    justify-content: center;
    padding: rem(40) rem(25);
    max-width: rem(950);
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    @include mq("sp") {
      flex-direction: column;
      align-items: center;
    }
  }
  &__list {
    max-width: rem(300);
    width: 100%;
    @include mq("sp") {
      margin-bottom: rem(15);
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__item {
    text-align: center;
    font-size: rem(18);
    font-weight: bold;
    letter-spacing: 0.05em;
    margin-top: rem(15);
    &:first-child {
      margin-top: 0;
    }
  }
}