.header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding: rem(15);
  background-color: #fff;
  z-index: 999;
  @include mq("sp") {
    
  }
}

/*========= ナビゲーションのためのCSS ===============*/

#g-nav{
  /*position:fixed;にし、z-indexの数値を大きくして前面へ*/
  position:fixed;
  z-index: 999;
  /*ナビのスタート位置と形状*/
top:0;
  right: -120%;
width:100%;
  height: 100vh;/*ナビの高さ*/
background:#fff;
  /*動き*/
transition: all 0.6s;

}

/*アクティブクラスがついたら位置を0に*/
#g-nav.panelactive{
  right: 0;
}

/*ナビゲーションの縦スクロール*/
#g-nav.panelactive #g-nav-list{
  /*ナビの数が増えた場合縦スクロール*/
  position: fixed;
  z-index: 999; 
  width: 100%;
  height: 100vh;/*表示する高さ*/
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}

/*ナビゲーション*/
#g-nav ul {
  /*ナビゲーション天地中央揃え*/
  position: absolute;
  z-index: 999;
  top:50%;
  left:10%;
  transform: translateY(-50%);
}

/*リストのレイアウト設定*/

#g-nav li{
  list-style: none;
}

#g-nav li a{
color: #31B1E3;
text-decoration: none;
padding:rem(20);
display: block;
text-transform: uppercase;
letter-spacing: 0.1em;
font-weight: bold;
}

/*========= ボタンのためのCSS ===============*/
.openbtn{
position:fixed;
  z-index: 9999;/*ボタンを最前面に*/
top:rem(7);
right: 10px;
cursor: pointer;
  width: 50px;
  height:50px;
  display: none;
  @include mq(sp){
    display: block;
	}
}

/*×に変化*/	
.openbtn span{
  display: inline-block;
  transition: all .4s;
  position: absolute;
  left: 14px;
  height: 3px;
  border-radius: 2px;
background-color: #31B1E3;
  width: 45%;
}

.openbtn span:nth-of-type(1) {
top:15px;	
}

.openbtn span:nth-of-type(2) {
top:23px;
}

.openbtn span:nth-of-type(3) {
top:31px;
}

.openbtn.active span:nth-of-type(1) {
  top: 18px;
  left: 18px;
  transform: translateY(6px) rotate(-45deg);
  width: 30%;
  background-color: #31B1E3;
}

.openbtn.active span:nth-of-type(2) {
  opacity: 0;
  background-color: #31B1E3;
}

.openbtn.active span:nth-of-type(3){
  background-color: #31B1E3;
  top: 30px;
  left: 18px;
  transform: translateY(-6px) rotate(45deg);
  width: 30%;
}