.about {
  padding: rem(120) rem(15) rem(90) rem(15);
  @include mq("sp") {
    padding-top: rem(60);
  }
  &__bigText {
    font-size: rem(24);
    color: #31B1E3;
    font-weight: bold;
    text-align: center;
    margin-top: rem(90);
    @include mq("sp") {
      font-size: rem(20);
    }
  }
  &__text {
    font-size: rem(16);
    font-weight: bold;
    text-align: center;
    margin-top: rem(25);
    @include mq("sp") {
      font-size: rem(14);
      line-height: 1.75;
    }
  }
  &__unit {
    display: flex;
    justify-content: center;
    margin-top: rem(60);
    @include mq("sp") {
      flex-direction: column;
      align-items: center;
    }
  }
  &__card {
    max-width: rem(320);
    width: 100%;
    &:first-child {
      margin-right: rem(100);
      @include mq("sp") {
        margin-right: 0;
        margin-bottom: rem(60);
      }
    }
  }
  &__cardHead {
    background-color: #31B1E3;
    border-radius: rem(30);
    padding: rem(15) 0;
    text-align: center;
    font-size: rem(20);
    font-weight: bold;
    color: #fff;
    letter-spacing: 0.05em;
  }
  &__image {
    width: rem(200);
    height: auto;
    margin-right: auto;
    margin-left: auto;
    margin-top: rem(15);
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__cardText {
    margin-top: rem(15);
    font-size: rem(16);
    letter-spacing: 0.05em;
    @include mq("sp") {
      font-size: rem(14);
      font-weight: bold;
    }
  }
}