.message {
  padding: rem(90) rem(15);
  &__box {
    margin-top: rem(80);
    max-width: rem(720);
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    padding: rem(40);
    box-shadow: 0 0 rem(10) rgba($color: #000000, $alpha: .25);
    p {
      font-size: rem(16);
      letter-spacing: 0.05em;
      line-height: 2;
      font-weight: bold;
    }
  }
}