.footer {
  padding: rem(90) rem(15) rem(15) rem(15);
  background-color: #EFF9FD;
  &__inner {
    max-width: rem(600);
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    font-size: 0;
  }
  &__companySub {
    font-size: rem(20);
    letter-spacing: 0.05em;
    font-weight: bold;
    @include mq("sp") {
      font-size: rem(16);
    }
  }
  &__companyMain {
    font-size: rem(36);
    color: #31B1E3;
    letter-spacing: 0.05em;
    font-weight: bold;
    @include mq("sp") {
      font-size: rem(28);
    }
    span {
      font-size: rem(16);
    }
  }
  &__companyKana {
    font-size: rem(16);
    color: #31B1E3;
    letter-spacing: 0.05em;
    font-weight: bold;
  }
  &__info {
    display: flex;
    margin-top: rem(40);
    @include mq("sp") {
      flex-direction: column;
    }
  }
  &__infoItem {
    max-width: rem(200);
    width: 100%;
    @include mq("sp") {
      margin-bottom: rem(40);
      max-width: 100%;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__infoHead {
    margin-bottom: rem(20);
    font-size: rem(24);
    font-weight: bold;
  }
  &__infoDetail {
    font-size: rem(18);
    font-weight: bold;
    margin-bottom: rem(10);
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__copyright {
    margin-top: rem(60);
    font-size: rem(12);
    text-align: center;
  }
}