.heading {
  text-align: center;
  position: relative;
  font-size: rem(36);
  letter-spacing: 0.05em;
  @include mq("sp") {
    font-size: rem(28);
  }
  &::after {
    font-size: rem(28);
    letter-spacing: 0.05em;
    position: absolute;
    left: 50%;
    bottom: rem(-35);
    transform: translateX(-50%);
    color: #31B1E3;
    font-weight: regular;
    @include mq("sp") {
      font-size: rem(18);
      bottom: rem(-25);
    }
  }
  &--about {
    &::after {
      content: "about";
    }
  }
  &--image {
    &::after {
      content: "image";
    }
  }
  &--message {
    &::after {
      content: "message";
    }
  }
}