.fv {
    margin-top: rem(70);
    position: relative;
    &__body {
        position: absolute;
        top: 45%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 10;
        @include mq("sp") {
            top: 40%;
        }
    }
    &__main {
        font-size: rem(44);
        letter-spacing: 0.05em;
        text-shadow:1px 1px 0 rgba($color: #fff, $alpha: .5), -1px -1px 0 rgba($color: #fff, $alpha: .5),
        -1px 1px 0 rgba($color: #fff, $alpha: .5), 1px -1px 0 rgba($color: #fff, $alpha: .5),
        0px 1px 0 rgba($color: #fff, $alpha: .5),  0-1px 0 rgba($color: #fff, $alpha: .5),
        -1px 0 0 rgba($color: #fff, $alpha: .5), 1px 0 0 rgba($color: #fff, $alpha: .5);
        font-weight: bold;
        color: #000;
        text-align: center;
        white-space: nowrap;
        @include mq("sp") {
            font-size: rem(24);
        }
    }
    &__sub {
        position: absolute;
        bottom: rem(50);
        left: 50%;
        transform: translateX(-50%);
        z-index: 10;
        @include mq("sp") {
            bottom: rem(40);
        }
    }
    &__company {
        font-size: rem(36);
        letter-spacing: 0.05em;
        text-shadow:1px 1px 0 rgba($color: #fff, $alpha: .5), -1px -1px 0 rgba($color: #fff, $alpha: .5),
        -1px 1px 0 rgba($color: #fff, $alpha: .5), 1px -1px 0 rgba($color: #fff, $alpha: .5),
        0px 1px 0 rgba($color: #fff, $alpha: .5),  0-1px 0 rgba($color: #fff, $alpha: .5),
        -1px 0 0 rgba($color: #fff, $alpha: .5), 1px 0 0 rgba($color: #fff, $alpha: .5);
        font-weight: bold;
        color: #000;
        text-align: center;
        white-space: nowrap;
        @include mq("sp") {
            font-size: rem(18);
        }
    }
}

/*==================================================
スライダーのためのcss
===================================*/
.slider {
    position:relative;
	z-index: 1;
	/*↑z-indexの値をh1のz-indexの値よりも小さくして背景に回す*/
	height: rem(530);/*スライダー全体の縦幅を画面の高さいっぱい（100vh）にする*/
    margin-right: auto;
    margin-left: auto;
    max-width: rem(1000);
    width: 100%;
    @include mq("sp") {
        height: rem(300);
    }
}
/*　背景画像設定　*/

.slider-item01 {
    background:url(../img/fv.jpg);
}

.slider-item02 {
    background:url(../img/fv-2.jpg);
}

.slider-item03 {
    background:url(../img/fv-3.jpg);
}

.slider-item {
    width: 100%;/*各スライダー全体の横幅を画面の高さいっぱい（100%）にする*/
    height:rem(530);/*各スライダー全体の縦幅を画面の高さいっぱい（100vh）にする*/
    background-repeat: no-repeat;/*背景画像をリピートしない*/
    background-position: center;/*背景画像の位置を中央に*/
    background-size: cover;/*背景画像が.slider-item全体を覆い表示*/
    @include mq("sp") {
        height: rem(225);
    }
}