/*========= ナビゲーションのためのCSS ===============*/
#g-nav {
  /*position:fixed;にし、z-indexの数値を大きくして前面へ*/
  position: fixed;
  z-index: 999;
  /*ナビのスタート位置と形状*/
  top: 0;
  right: -120%;
  width: 100%;
  height: 100vh;
  /*ナビの高さ*/
  background: #fff;
  /*動き*/
  transition: all 0.6s;
}

/*アクティブクラスがついたら位置を0に*/
#g-nav.panelactive {
  right: 0;
}

/*ナビゲーションの縦スクロール*/
#g-nav.panelactive #g-nav-list {
  /*ナビの数が増えた場合縦スクロール*/
  position: fixed;
  z-index: 999;
  width: 100%;
  height: 100vh;
  /*表示する高さ*/
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}

/*ナビゲーション*/
#g-nav ul {
  /*ナビゲーション天地中央揃え*/
  position: absolute;
  z-index: 999;
  top: 50%;
  left: 10%;
  transform: translateY(-50%);
}

/*リストのレイアウト設定*/
#g-nav li {
  list-style: none;
}

#g-nav li a {
  color: #31B1E3;
  text-decoration: none;
  padding: 1.25rem;
  display: block;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  font-weight: bold;
}

/*========= ボタンのためのCSS ===============*/
.openbtn {
  position: fixed;
  z-index: 9999;
  /*ボタンを最前面に*/
  top: 0.4375rem;
  right: 10px;
  cursor: pointer;
  width: 50px;
  height: 50px;
  display: none;
}

@media screen and (max-width: 767px) {
  .openbtn {
    display: block;
  }
}

/*×に変化*/
.openbtn span {
  display: inline-block;
  transition: all .4s;
  position: absolute;
  left: 14px;
  height: 3px;
  border-radius: 2px;
  background-color: #31B1E3;
  width: 45%;
}

.openbtn span:nth-of-type(1) {
  top: 15px;
}

.openbtn span:nth-of-type(2) {
  top: 23px;
}

.openbtn span:nth-of-type(3) {
  top: 31px;
}

.openbtn.active span:nth-of-type(1) {
  top: 18px;
  left: 18px;
  transform: translateY(6px) rotate(-45deg);
  width: 30%;
  background-color: #31B1E3;
}

.openbtn.active span:nth-of-type(2) {
  opacity: 0;
  background-color: #31B1E3;
}

.openbtn.active span:nth-of-type(3) {
  background-color: #31B1E3;
  top: 30px;
  left: 18px;
  transform: translateY(-6px) rotate(45deg);
  width: 30%;
}

.heading {
  text-align: center;
  position: relative;
  font-size: 2.25rem;
  letter-spacing: 0.05em;
}

@media screen and (max-width: 767px) {
  .heading {
    font-size: 1.75rem;
  }
}

.heading::after {
  font-size: 1.75rem;
  letter-spacing: 0.05em;
  position: absolute;
  left: 50%;
  bottom: -2.1875rem;
  transform: translateX(-50%);
  color: #31B1E3;
  font-weight: regular;
}

@media screen and (max-width: 767px) {
  .heading::after {
    font-size: 1.125rem;
    bottom: -1.5625rem;
  }
}

.heading--about::after {
  content: "about";
}

.heading--image::after {
  content: "image";
}

.heading--message::after {
  content: "message";
}

.image {
  padding: 5.625rem 0.9375rem;
  background-color: #EFF9FD;
}

.image__main {
  margin-top: 5.625rem;
  max-width: 59.375rem;
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  height: auto;
}

.image__main img {
  width: 100%;
  height: 100%;
}

.message {
  padding: 5.625rem 0.9375rem;
}

.message__box {
  margin-top: 5rem;
  max-width: 45rem;
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  padding: 2.5rem;
  box-shadow: 0 0 0.625rem rgba(0, 0, 0, 0.25);
}

.message__box p {
  font-size: 1rem;
  letter-spacing: 0.05em;
  line-height: 2;
  font-weight: bold;
}

.nav__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav__logoImage {
  width: 3.125rem;
  height: auto;
}

.nav__logoImage img {
  width: 100%;
  height: 100%;
}

@media screen and (max-width: 767px) {
  .nav__logoImage {
    width: 2.5rem;
  }
}

.nav__list {
  display: flex;
  align-items: center;
}

@media screen and (max-width: 767px) {
  .nav__list.inFooter {
    flex-direction: column;
    margin-top: 3rem;
    align-items: flex-start;
  }
}

.nav__item {
  padding: 0.4375rem 0.9375rem;
  border-right: 0.0625rem solid #E5E5E5;
}

.nav__item a {
  font-size: 0.875rem;
  font-weight: 600;
  letter-spacing: 0.15em;
  line-height: 1.4;
}

@media screen and (max-width: 767px) {
  .nav__item {
    border: none;
    padding-left: 0;
  }
}

.nav__item--tel {
  border: none;
  padding-left: 2.5rem;
  position: relative;
}

.nav__item--tel::before {
  content: '';
  width: 1.25rem;
  height: 1.25rem;
  background-image: url(../img/tel.png);
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0.9375rem;
}

@media screen and (max-width: 767px) {
  .nav__item--tel {
    padding-left: 1.25rem;
  }
}

.supplement {
  padding: 5.625rem 0.9375rem;
  background-image: url(../img/bg02.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.supplement__box {
  margin-top: 3.75rem;
  border: 0.125rem solid #fff;
  display: flex;
  justify-content: center;
  padding: 2.5rem 1.5625rem;
  max-width: 59.375rem;
  width: 100%;
  margin-right: auto;
  margin-left: auto;
}

@media screen and (max-width: 767px) {
  .supplement__box {
    flex-direction: column;
    align-items: center;
  }
}

.supplement__list {
  max-width: 18.75rem;
  width: 100%;
}

@media screen and (max-width: 767px) {
  .supplement__list {
    margin-bottom: 0.9375rem;
  }
  .supplement__list:last-child {
    margin-bottom: 0;
  }
}

.supplement__item {
  text-align: center;
  font-size: 1.125rem;
  font-weight: bold;
  letter-spacing: 0.05em;
  margin-top: 0.9375rem;
}

.supplement__item:first-child {
  margin-top: 0;
}

@media screen and (max-width: 767px) {
  .pc {
    display: none;
  }
}

.sp {
  display: none;
}

@media screen and (max-width: 767px) {
  .sp {
    display: block;
  }
}
