.nav {
  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logoImage {
    width: rem(50);
    height: auto;
    img {
      width: 100%;
      height: 100%;
    }
    @include mq(sp) {
      width: rem(40);
    }
  }
  &__list {
    display: flex;
    align-items: center;
    &.inFooter {
      @include mq("sp") {
        flex-direction: column;
        margin-top: rem(48);
        align-items: flex-start;
      }
    }
  }
  &__item {
    padding: rem(7) rem(15);
    border-right: rem(1) solid #E5E5E5;
    a {
      font-size: rem(14);
      font-weight: 600;
      letter-spacing: 0.15em;
      line-height: 1.4;
      @include mq("sp") {
        
      }
    }
    @include mq("sp") {
      border: none;
      padding-left: 0;
    }
    &--tel {
      border: none;
      padding-left: rem(40);
      position: relative;
      &::before {
        content: '';
        width: rem(20);
        height: rem(20);
        background-image: url(../img/tel.png);
        background-repeat: no-repeat;
        background-size: cover;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: rem(15);
      }
      @include mq("sp") {
        padding-left: rem(20);
      }
    }
  }
}