.btn {
  &__unit {
    margin-top: rem(80);
  }
  display: block;
  width: rem(320);
  margin-right: auto;
  margin-left: auto;
  background-color: #31B1E3;
  border-radius: rem(30);
  padding: rem(15) 0;
  text-align: center;
  font-size: rem(18);
  font-weight: bold;
  color: #fff;
  letter-spacing: 0.05em;
  position: relative;
  &::after {
    content: "";
    width: rem(30);
    height: rem(30);
    background-image: url(../img/arrow.png);
    background-repeat: no-repeat;
    background-size: cover;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: rem(18);
  }
  &--line {
    margin-top: rem(25);
    background-color: #00B900;
  }
  &--flowPC {
    width: rem(60);
    height: rem(240);
    padding: rem(25) rem(16) 0 rem(15);
    position: fixed;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
    -ms-writing-mode: tb-rl;
    writing-mode: vertical-rl;
    z-index: 999;
    &::after {
      top: rem(18);
      right: 50%;
      transform: translateX(50%);
    }
  }
  &--flowSP {
    width: 100%;
    border-radius: 0;
    padding-bottom: rem(14);
    background-color: #1877f2;
    position: fixed;
    bottom: rem(0);
    transform: translateX(-50%);
    left: 50%;
    z-index: 999;
  }
}